import React, { Component, Suspense } from 'react';
import Select from 'react-select';
import { Accordion, OverlayTrigger, Tooltip } from 'react-bootstrap';
// import { baseUrl } from '../../../../Assets/AssetsHelper';
import { PopUpOption, ColumnOption, HideOlderOption } from './Utils'
import PerfectScrollbar from 'react-perfect-scrollbar'
import { Calendar } from 'react-date-range';
const ColorPicker = React.lazy(() => import('../../../CommonComponents/ColorPicker'));
const Background = React.lazy(() => import('./Elements/Background'));
const CustomCss = React.lazy(() => import('./Elements/CustomCss'));
const OnSiteUploads = React.lazy(() => import('./Elements/OnSiteUpload'));
const DesignCardStyle = React.lazy(() => import('./Elements/CardStyle'));
class ModernCardTemplate extends Component {
    state = {
        background: { status: false, color: "#fff", image: null, transparent: 0, backgroundImageId: 0 },
        cardStyle: { cardColor: "#000000", borderRadius: 10, fontFamily: null, fontVarient: null, fontSize: 15, textColor: "#fff", authorColor: "#fff", socialIconColor: "#fff", },
        advanced: { time: false, author: false, hideContent: false, shareOptions: false, socialActions: false },
        onSiteUpload: { bgColor: "#fff", borderRadius: 0, fontFamily: null, fontVarient: null, fontSize: 10, textColor: "#fff", buttonText: "Scan QR or Tap here to upload UGC", buttonStatus: false, qrCodeStatus: false },
        themeSettings: { suffleAssets: false, showmore: false, autoLoad: false, trimContent: false, bestFit: false, popupSlideShow: true, hideOldAssets: { status: false, range: null, showHideOldPost: new Date() }, postPopUp: null, popUpUrl: null, numberOfPosts: 20, columns: { label: "Responsive", value: 0 }, postPadding: 15, hideTextTypeAssets: false, minPostWitdh: 300, playFullVideo: false, hideUrls: false, timePerSlide: 0, scrollSpeed: 0, popUpduration: 15 },
        customCss: { cssStatus: false, css: "" },

    }
    componentDidMount() {
        const { props } = this
        const { themeDefaultOption } = props.selectedThemeData
        const { onSiteSetting, fonts } = props
        const cardFontStyle = themeDefaultOption.css_font ? fonts.filter((itemf) => itemf.family == themeDefaultOption.css_font)[0] : fonts[0]
        var font_varient = themeDefaultOption.font_varient && cardFontStyle.variants ? { label: cardFontStyle.variants.filter((varient) => varient == themeDefaultOption.font_varient)[0], value: cardFontStyle.variants.filter((varient) => varient == themeDefaultOption.font_varient)[0] } : {}

        const onSiteFontStyle = onSiteSetting && onSiteSetting.onsite_css_font_btn ? fonts.filter((itemf) => itemf.family == onSiteSetting.onsite_css_font_btn)[0] : fonts[0]
        var onSitefont_varient = onSiteSetting && onSiteSetting.onsite_font_varient_btn && onSiteFontStyle && onSiteFontStyle.variants ? { label: onSiteFontStyle.variants.filter((varient) => varient == onSiteSetting.onsite_font_varient_btn)[0], value: onSiteFontStyle.variants.filter((varient) => varient == onSiteSetting.onsite_font_varient_btn)[0] } : {}

        this.setState({
            background: { status: themeDefaultOption.transparent == 1 ? false : true, color: themeDefaultOption.backgroundColor ? themeDefaultOption.backgroundColor : "#fff", image: null, transparent: themeDefaultOption.transparent == 1 ? 1 : 0, backgroundImageId: 0 },
            cardStyle: { cardColor: themeDefaultOption.cardColor ? themeDefaultOption.cardColor : "#000000", borderRadius: 10, fontFamily: cardFontStyle, fontVarient: font_varient, fontSize: themeDefaultOption.fontSize ? themeDefaultOption.fontSize : 15, textColor: themeDefaultOption.fontColor ? themeDefaultOption.fontColor : "#fff", authorColor: themeDefaultOption.authorColor ? themeDefaultOption.authorColor : "#fff", socialIconColor: themeDefaultOption.iconColor ? themeDefaultOption.iconColor : "#fff", iconType: themeDefaultOption.iconType == 1 ? true : false },
            advanced: { time: themeDefaultOption.postTime ? themeDefaultOption.postTime == 1 ? true : false : true, author: themeDefaultOption.postAuthor ? themeDefaultOption.postAuthor == 1 ? true : false : true, hideContent: themeDefaultOption.hideContent == 1 ? true : false, shareOptions: themeDefaultOption.shareOption == 1 ? true : false, socialActions: themeDefaultOption.socialAction == 1 ? true : false },

            onSiteUpload: { bgColor: onSiteSetting.onsite_btn_color ? onSiteSetting.onsite_btn_color : "#fff", borderRadius: onSiteSetting.btn_radius ? onSiteSetting.btn_radius : 0, fontFamily: onSiteFontStyle, fontVarient: onSitefont_varient, fontSize: onSiteSetting.btn_fontSize ? onSiteSetting.btn_fontSize : 10, textColor: onSiteSetting.onsite_btn_txt_color ? onSiteSetting.onsite_btn_txt_color : "#fff", buttonText: onSiteSetting.onsite_qr_code_btn_text ? onSiteSetting.onsite_qr_code_btn_text : "Scan QR or Tap here to upload UGC", buttonStatus: onSiteSetting.onsite_status == 1 ? true : false, qrCodeStatus: onSiteSetting.onsite_qr_code_status == 1 ? true : false },


            themeSettings: { suffleAssets: themeDefaultOption.randomizePost == 1 ? true : false, showmore: themeDefaultOption.loadMoreStatus ? themeDefaultOption.loadMoreStatus == 1 ? true : false : true, autoLoad: themeDefaultOption.autoScrollStatus == 1 ? true : false, trimContent: themeDefaultOption.trimcontent == 1 ? true : false, bestFit: themeDefaultOption.fitRow ? themeDefaultOption.fitRow == 1 ? true : false : true, popupSlideShow: themeDefaultOption.popupSlideShow ? themeDefaultOption.popupSlideShow == 1 ? true : false : true, hideOldAssets: { status: themeDefaultOption.hidePosts == 1 ? true : false, range: themeDefaultOption.showHideOlderPost ? themeDefaultOption.showHideOlderPost : null, showHideOldPost: new Date() }, postPopUp: themeDefaultOption.postFeatured && String(themeDefaultOption.postFeatured).length > 0 ? PopUpOption.filter((itemf) => itemf.value == themeDefaultOption.postFeatured)[0] : PopUpOption[1], popUpUrl: null, numberOfPosts: themeDefaultOption.numberOfPosts ? themeDefaultOption.numberOfPosts : 20, columns: themeDefaultOption.columns ? ColumnOption.filter((itemf) => itemf.value == themeDefaultOption.columns)[0] : { label: "Responsive", value: 0 }, postPadding: themeDefaultOption.padding ? themeDefaultOption.padding : 3, hideTextTypeAssets: false, minPostWitdh: themeDefaultOption.minimumPostWidth ? themeDefaultOption.minimumPostWidth : 300, playFullVideo: false, hideUrls: themeDefaultOption.hideUrls == 1 ? true : false, timePerSlide: themeDefaultOption.slideDelay ? themeDefaultOption.slideDelay : 15, scrollSpeed: 0, popUpduration: themeDefaultOption.slideDuration },


            customCss: { cssStatus: themeDefaultOption.cssStatus == 1 ? true : false, css: themeDefaultOption.css ? themeDefaultOption.css : "" }

        }, () => this.props.tempFormData(this.state))

    }




    onClickEnableBackground = event => {
        const { background } = this.state;
        background.status = event.target.checked
        if (!event.target.checked) background.transparent = 1
        else background.transparent = 0
        this.setState({ background })
    }
    onClickBackgroundColor = (color) => {
        const { background } = this.state;
        background.color = color
        this.setState({ background })
    }
    onClickTouploadImage = (value, backgroundImageId, callBack) => {
        const { background } = this.state;
        background.image = value
        background.backgroundImageId = backgroundImageId
        this.setState({ background }, () => callBack())
    }

    backgroundImageRemove = event => {
        const { background } = this.state;
        background.image = null
        background.backgroundImageId = 0
        this.setState({ background })
    }




    onClickEnableTime = event => {
        const { advanced } = this.state;
        advanced.time = event.target.checked
        this.setState({ advanced })
    }
    onClickEnableAuthor = event => {
        const { advanced } = this.state;
        advanced.author = event.target.checked
        this.setState({ advanced })
    }
    onClickEnableHideContent = event => {
        const { advanced } = this.state;
        advanced.hideContent = event.target.checked
        this.setState({ advanced })
    }

    onClickEnableShareOptions = event => {
        const { advanced } = this.state;
        advanced.shareOptions = event.target.checked
        this.setState({ advanced })
    }
    onClickEnableSocialActions = event => {
        const { advanced } = this.state;
        advanced.socialActions = event.target.checked
        this.setState({ advanced })
    }


    onClickEnableCustomCss = event => {
        const { customCss } = this.state;
        customCss.cssStatus = event.target.checked
        this.setState({ customCss })
    }

    onChangeCss = event => {
        const { customCss } = this.state;
        customCss.css = event
        this.setState({ customCss })
    }



    onClickEnableSuffleAssets = event => {
        const { themeSettings } = this.state;
        themeSettings.suffleAssets = event.target.checked
        this.setState({ themeSettings })
    }
    onClickHideTextTypeAssets = event => {
        const { themeSettings } = this.state;
        themeSettings.hideTextTypeAssets = event.target.checked
        this.setState({ themeSettings })
    }
    onClickShowMore = event => {
        const { themeSettings } = this.state;
        themeSettings.showmore = event.target.checked
        themeSettings.autoLoad = event.target.checked ? false : true
        this.setState({ themeSettings })
    }
    onClickAutoLoad = event => {
        const { themeSettings } = this.state;
        themeSettings.autoLoad = event.target.checked
        themeSettings.showmore = event.target.checked ? false : true
        this.setState({ themeSettings })
    }
    onClickTrimContent = event => {
        const { themeSettings } = this.state;
        themeSettings.trimContent = event.target.checked
        this.setState({ themeSettings })
    }

    onClickBestFit = event => {
        const { themeSettings } = this.state;
        themeSettings.bestFit = event.target.checked
        this.setState({ themeSettings })
    }
    onClickPopUpSlideShow = event => {
        const { themeSettings } = this.state;
        themeSettings.popupSlideShow = event.target.checked
        this.setState({ themeSettings })
    }
    onClickHideOldAssets = event => {
        const { themeSettings } = this.state;
        themeSettings.hideOldAssets.status = event.target.checked
        this.setState({ themeSettings })
    }
    onSelectHideOlderPost = (event) => {
        const { themeSettings } = this.state;
        themeSettings.hideOldAssets.range = event
        this.setState({ themeSettings })
    }

    onSelectHideDateRange = (event) => {
        const { themeSettings } = this.state;
        themeSettings.hideOldAssets.showHideOldPost = event
        this.setState({ themeSettings })
    }

    onNumberOfPosts = event => {
        const { themeSettings } = this.state;
        if (event.target.value && parseInt(event.target.value) <= 100) {
            themeSettings.numberOfPosts = event.target.value
            this.setState({ themeSettings })
        }
    }
    onPostPadding = event => {
        const { themeSettings } = this.state;
        themeSettings.postPadding = event.target.value > 30 ? 30 : event.target.value
        this.setState({ themeSettings })
    }
    onMinPostWitdh = event => {
        const { themeSettings } = this.state;
        themeSettings.minPostWitdh = event.target.value
        this.setState({ themeSettings })
    }
    onSelectPostPopUp = event => {
        const { themeSettings } = this.state;
        themeSettings.postPopUp = event
        this.setState({ themeSettings })
    }
    onPopUpUrl = event => {
        const { themeSettings } = this.state;
        themeSettings.popUpUrl = event.target.value
        this.setState({ themeSettings })
    }


    onChangeCardColor = (cardColor) => {
        const { cardStyle } = this.state;
        cardStyle.cardColor = cardColor
        this.setState({ cardStyle })
    }
    onCardBorderRadius = event => {
        const { cardStyle } = this.state;
        cardStyle.borderRadius = event.target.value > 30 ? 30 : event.target.value
        this.setState({ cardStyle })
    }
    onCardFontFamily = (event) => {
        const { cardStyle } = this.state;
        cardStyle.fontFamily = event
        this.setState({ cardStyle })
    }
    onCardFontVarient = (event) => {
        const { cardStyle } = this.state;
        cardStyle.fontVarient = event
        this.setState({ cardStyle })
    }
    onCardFontSize = event => {
        const { cardStyle } = this.state;
        cardStyle.fontSize = event.target.value > 30 ? 30 : event.target.value
        this.setState({ cardStyle })
    }
    onCardTextColor = (value) => {
        const { cardStyle } = this.state;
        cardStyle.textColor = value
        this.setState({ cardStyle })
    }
    onCardAuthorColor = (value) => {
        const { cardStyle } = this.state;
        cardStyle.authorColor = value
        this.setState({ cardStyle })
    }
    onCardSocialColor = (value) => {
        const { cardStyle } = this.state;
        cardStyle.socialIconColor = value
        this.setState({ cardStyle })
    }
    onClickIconType = event => {
        const { cardStyle } = this.state;
        cardStyle.iconType = event.target.checked
        this.setState({ cardStyle })
    }

    onOnSiteBgColor = (value) => {
        const { onSiteUpload } = this.state;
        onSiteUpload.bgColor = value
        this.setState({ onSiteUpload })
    }
    onOnSiteBorderRadius = event => {
        const { onSiteUpload } = this.state;
        onSiteUpload.borderRadius = event.target.value
        this.setState({ onSiteUpload })
    }


    onOnSiteFontFamily = (event) => {
        const { onSiteUpload } = this.state;
        onSiteUpload.fontFamily = event
        this.setState({ onSiteUpload })
    }
    onOnSiteFontVarient = (event) => {
        const { onSiteUpload } = this.state;
        onSiteUpload.fontVarient = event
        this.setState({ onSiteUpload })
    }
    onOnSiteFontSize = event => {
        const { onSiteUpload } = this.state;
        onSiteUpload.fontSize = event.target.value > 30 ? 30 : event.target.value
        this.setState({ onSiteUpload })
    }

    onOnSiteTextColor = (value) => {
        const { onSiteUpload } = this.state;
        onSiteUpload.textColor = value
        this.setState({ onSiteUpload })
    }
    onOnSiteButtonText = event => {
        const { onSiteUpload } = this.state;
        onSiteUpload.buttonText = event.target.value
        this.setState({ onSiteUpload })
    }

    onOnSiteButtonStatus = event => {
        const { onSiteUpload } = this.state;
        onSiteUpload.buttonStatus = event.target.checked
        this.setState({ onSiteUpload })
    }
    onOnSiteQrCodeStatus = event => {
        const { onSiteUpload } = this.state;
        onSiteUpload.qrCodeStatus = event.target.checked
        this.setState({ onSiteUpload })
    }


    componentDidUpdate(prevProps, prevState) {
        if (prevState != this.state) this.props.tempFormData(this.state)
    }

    render() {
        const { selectedThemeData, fonts } = this.props
        const { background, cardStyle, advanced, onSiteUpload, buttonStatus, themeSettings, customCss, backgroundModal } = this.state;

        return <Accordion defaultActiveKey="1">
            <Background onClickEnableBackground={this.onClickEnableBackground} onClickBackgroundColor={this.onClickBackgroundColor} background={background} onClickTouploadImage={this.onClickTouploadImage} backgroundImageRemove={this.backgroundImageRemove} />
            <DesignCardStyle cardStyle={cardStyle} onChangeCardColor={this.onChangeCardColor} onCardBorderRadius={this.onCardBorderRadius} onCardFontSize={this.onCardFontSize} fonts={fonts} onCardFontFamily={this.onCardFontFamily} onCardFontVarient={this.onCardFontVarient} onCardTextColor={this.onCardTextColor} onCardAuthorColor={this.onCardAuthorColor} onCardSocialColor={this.onCardSocialColor} onClickIconType={this.onClickIconType} />

            <Accordion.Item eventKey="4" className="pb-3">
                <Accordion.Header className="w-100">
                    <span className="fs-9 fw-medium">Advanced</span>
                </Accordion.Header>
                <Accordion.Body className="border rounded-1 mt-1 p-2">
                    <label className="form-check form-switch py-2 align-items-center justify-content-between border-bottom h-40px py-1">
                        <span className="form-check-label ps-0 fs-9 fw-medium text-gray-800">Time</span>
                        <input className="form-check-input" type="checkbox" name="shareIcons" checked={advanced.time} onChange={this.onClickEnableTime} />
                    </label>
                    <label className="form-check form-switch py-2 align-items-center justify-content-between border-bottom h-40px py-1">
                        <span className="form-check-label ps-0 fs-9 fw-medium text-gray-800">Author Details</span>
                        <input className="form-check-input" type="checkbox" name="shareIcons" checked={advanced.author} onChange={this.onClickEnableAuthor} />
                    </label>
                    <label className="form-check form-switch py-2 align-items-center justify-content-between border-bottom h-40px py-1">
                        <span className="form-check-label ps-0 fs-9 fw-medium text-gray-800">Share Options</span>
                        <input className="form-check-input" type="checkbox" name="shareIcons" checked={advanced.shareOptions} onChange={this.onClickEnableShareOptions} />
                    </label>
                    <label className="form-check form-switch py-2 align-items-center justify-content-between border-bottom h-40px py-1">
                        <span className="form-check-label ps-0 fs-9 fw-medium text-gray-800">Social Actions</span>
                        <input className="form-check-input" type="checkbox" name="shareIcons" checked={advanced.socialActions} onChange={this.onClickEnableSocialActions} />
                    </label>
                    <label className="form-check form-switch py-2 align-items-center justify-content-between h-40px py-1">
                        <span className="form-check-label ps-0 fs-9 fw-medium text-gray-800">Hide Content</span>
                        <input className="form-check-input" type="checkbox" name="shareIcons" checked={advanced.hideContent} onChange={this.onClickEnableHideContent} />
                    </label>
                </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="6" className="pb-3">
                <Accordion.Header className="w-100">
                    <span className="fs-9 fw-medium">Theme Settings</span>
                </Accordion.Header>
                <Accordion.Body className="border rounded-1 mt-1 p-2">
                    <label className="form-check form-switch py-2 align-items-center justify-content-between border-bottom h-40px py-1">
                        <span className="form-check-label ps-0 fs-9 fw-medium text-gray-800 d-flex">Shuffle Assets
                            <OverlayTrigger placement="top" overlay={<Tooltip>Randomly shuffles your assets, regardless of date or time.</Tooltip>}>
                                <i className="ri-information-fill icon-6 ms-1 text-muted fw-normal"></i>
                            </OverlayTrigger>
                        </span>
                        <input className="form-check-input" type="checkbox" name="shareIcons" checked={themeSettings.suffleAssets} onChange={this.onClickEnableSuffleAssets} />
                    </label>
                    <label className="form-check form-switch py-2 align-items-center justify-content-between border-bottom h-40px py-1">
                        <span className="form-check-label ps-0 fs-9 fw-medium text-gray-800 d-flex">Show More Button
                            <OverlayTrigger placement="top" overlay={<Tooltip>Toggle the visibility of the Show More button.</Tooltip>}>
                                <i className="ri-information-fill icon-6 ms-1 text-muted fw-normal"></i>
                            </OverlayTrigger>
                        </span>
                        <input className="form-check-input" type="checkbox" name="shareIcons" checked={themeSettings.showmore} onChange={this.onClickShowMore} />
                    </label>
                    <label className="form-check form-switch py-2 align-items-center justify-content-between border-bottom h-40px py-1">
                        <span className="form-check-label ps-0 fs-9 fw-medium text-gray-800 d-flex">Auto Load
                            <OverlayTrigger placement="top" overlay={<Tooltip>Auto Load button.</Tooltip>}>
                                <i className="ri-information-fill icon-6 ms-1 text-muted fw-normal"></i>
                            </OverlayTrigger>
                        </span>
                        <input className="form-check-input" type="checkbox" name="shareIcons" checked={themeSettings.autoLoad} onChange={this.onClickAutoLoad} />
                    </label>
                    <label className="form-check form-switch py-2 align-items-center justify-content-between border-bottom h-40px py-1">
                        <span className="form-check-label ps-0 fs-9 fw-medium text-gray-800 d-flex">Hide Text Assets
                            <OverlayTrigger placement="top" overlay={<Tooltip>Hide text-only assets without any media.</Tooltip>}>
                                <i className="ri-information-fill icon-6 ms-1 text-muted fw-normal"></i>
                            </OverlayTrigger>
                        </span>
                        <input className="form-check-input" type="checkbox" name="shareIcons" checked={themeSettings.hideTextTypeAssets} onChange={this.onClickHideTextTypeAssets} />
                    </label>
                    <label className="form-check form-switch py-2 align-items-center justify-content-between border-bottom h-40px py-1">
                        <span className="form-check-label ps-0 fs-9 fw-medium text-gray-800 d-flex">Trim Content
                            <OverlayTrigger placement="top" overlay={<Tooltip>This will trim asset text after reaching the display limit of 3 lines.</Tooltip>}>
                                <i className="ri-information-fill icon-6 ms-1 text-muted fw-normal"></i>
                            </OverlayTrigger>
                        </span>
                        <input className="form-check-input" type="checkbox" name="shareIcons" checked={themeSettings.trimContent} onChange={this.onClickTrimContent} />
                    </label>
                    <div className="border-bottom">
                        <label className="form-check form-switch py-2 align-items-center justify-content-between h-40px py-1">
                            <span className="form-check-label ps-0 fs-9 fw-medium text-gray-800 d-flex">Hide Old Assets
                                <OverlayTrigger placement="top" overlay={<Tooltip>This feature hides old assets from the selected display location.</Tooltip>}>
                                    <i className="ri-information-fill icon-6 ms-1 text-muted fw-normal"></i>
                                </OverlayTrigger>
                            </span>
                            <input className="form-check-input" type="checkbox" name="shareIcons" checked={themeSettings.hideOldAssets.status} onChange={this.onClickHideOldAssets} />
                        </label>
                        {
                            themeSettings.hideOldAssets.status ? <> <div className="w-100 mb-2">
                                <Select className="selectpicker selectpicker-sm w-100" classNamePrefix="selectpicker" value={themeSettings.hideOldAssets.range} options={HideOlderOption} onChange={this.onSelectHideOlderPost} />
                            </div>
                                {themeSettings.hideOldAssets.range && Object.keys(themeSettings.hideOldAssets.range).length > 0 && themeSettings.hideOldAssets.range.value == 8 ?
                                    <div className="w-100 mb-2">
                                        <Calendar
                                            className="app_calendar"
                                            date={themeSettings.hideOldAssets.showHideOldPost}
                                            onChange={this.onSelectHideDateRange}
                                            color="#313A53"
                                        />
                                    </div> : null}</> : null
                        }

                    </div>

                    <label className="form-check form-switch py-2 align-items-center justify-content-between border-bottom h-40px py-1">
                        <span className="form-check-label ps-0 fs-9 fw-medium text-gray-800 d-flex">Popup Slider
                            <OverlayTrigger placement="top" overlay={<Tooltip>This feature allows you to scroll through other assets within the pop-up.</Tooltip>}>
                                <i className="ri-information-fill icon-6 ms-1 text-muted fw-normal"></i>
                            </OverlayTrigger>
                        </span>
                        <input className="form-check-input" type="checkbox" name="shareIcons" checked={themeSettings.popupSlideShow} onChange={this.onClickPopUpSlideShow} />
                    </label>

                    <div className="border-bottom">
                        <label className="form-check form-switch py-2 align-items-center justify-content-between h-40px py-1">
                            <span className="form-check-label ps-0 fs-9 fw-medium text-gray-800 d-flex">On Click Action
                                <OverlayTrigger placement="top" overlay={<Tooltip>Select the desired action when your asset is clicked.</Tooltip>}>
                                    <i className="ri-information-fill icon-6 ms-1 text-muted fw-normal"></i>
                                </OverlayTrigger>
                            </span>
                        </label>
                        <div className="w-100 mb-2">
                            <Select className="selectpicker selectpicker-sm w-100" classNamePrefix="selectpicker" value={themeSettings.postPopUp} options={PopUpOption} onChange={this.onSelectPostPopUp} isSearchable={false} />
                        </div>
                        {
                            themeSettings.postPopUp && Object.keys(themeSettings.postPopUp).length > 0 && themeSettings.postPopUp.value == 3 ? <div className="w-100 mb-2">
                                <input type="url" name="onClickLink" className="form-control form-control-sm" placeholder="Enter url..." value={themeSettings.popUpUrl} onChange={this.onPopUpUrl} />
                            </div> : null
                        }

                    </div>
                    <div className="border-bottom">
                        <label className="form-check form-switch py-2 align-items-center justify-content-between h-40px py-1">
                            <span className="form-check-label ps-0 fs-9 fw-medium text-gray-800 d-flex">Total Number of Assets to Display
                                <OverlayTrigger placement="top" overlay={<Tooltip>Maximum no. of assets to be displayed on live wall.</Tooltip>}>
                                    <i className="ri-information-fill icon-6 ms-1 text-muted fw-normal"></i>
                                </OverlayTrigger>
                            </span>
                        </label>
                        <div className="w-100 mb-2">
                            <input type="number" name="assetsCount" className="form-control form-control-sm" min="1" max="100" value={themeSettings.numberOfPosts} defaultValue={themeSettings.numberOfPosts} onChange={this.onNumberOfPosts} />
                        </div>
                    </div>

                    <div className="d-flex align-items-stretch flex-column justify-content-between border-bottom py-1">
                        <div className="">
                            <p className="my-2"><span className="fs-9 fw-medium">Minimum Asset Width</span></p>
                        </div>
                        <div className="d-flex align-items-center w-100 mb-1">
                            <div className="d-flex flex-grow-1 pe-3">
                                <div className="form-group pt-1 w-100">
                                    <input type="range" className="form-range" min="150" max="500" step="1" id="formControlRange" value={themeSettings.minPostWitdh} defaultValue={themeSettings.minPostWitdh} onChange={this.onMinPostWitdh} />
                                </div>
                            </div>
                            <div className="d-flex align-items-center">
                                <div className="form-group me-1">
                                    <input type="number" className="form-control form-control-sm w-54px" min="150" max="500" step="1" id="fontSize" value={themeSettings.minPostWitdh} defaultValue={themeSettings.minPostWitdh} onChange={this.onMinPostWitdh} />
                                </div>
                                px
                            </div>
                        </div>
                    </div>
                    <div className="d-flex align-items-stretch flex-column justify-content-between border-bottom py-1">
                        <div className="">
                            <p className="my-2"><span className="fs-9 fw-medium">Post Spacing</span></p>
                        </div>
                        <div className="d-flex align-items-center w-100 mb-1">
                            <div className="d-flex flex-grow-1 pe-3">
                                <div className="form-group pt-1 w-100">
                                    <input type="range" className="form-range" min="0" max="30" step="1" value={themeSettings.postPadding} defaultValue={themeSettings.postPadding} id="formControlRange" onChange={this.onPostPadding} />
                                </div>
                            </div>
                            <div className="d-flex align-items-center">
                                <div className="form-group me-1">
                                    <input type="number" className="form-control form-control-sm w-54px" id="fontSize" value={themeSettings.postPadding} defaultValue={themeSettings.postPadding} onChange={this.onPostPadding} />
                                </div>
                                px
                            </div>
                        </div>
                    </div>
                </Accordion.Body>
            </Accordion.Item>
            <OnSiteUploads onSiteUpload={onSiteUpload} onOnSiteBgColor={this.onOnSiteBgColor} onOnSiteBorderRadius={this.onOnSiteBorderRadius} onOnSiteFontSize={this.onOnSiteFontSize} onOnSiteTextColor={this.onOnSiteTextColor} onOnSiteButtonText={this.onOnSiteButtonText} onOnSiteButtonStatus={this.onOnSiteButtonStatus} onOnSiteQrCodeStatus={this.onOnSiteQrCodeStatus} onOnSiteFontFamily={this.onOnSiteFontFamily} onOnSiteFontVarient={this.onOnSiteFontVarient} fonts={fonts} />
            <CustomCss customCss={customCss} onClickEnableCustomCss={this.onClickEnableCustomCss} onChangeCss={this.onChangeCss} />
        </Accordion>
    }
}

export default ModernCardTemplate;